@import url('https://fonts.googleapis.com/css2?family=Jost:wght@700&display=swap');
.firstPage, 
.headerFirstPage,
.logo,
.cardBlock,
.footer {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #425262;
}
.headerFirstPage {
    height: 20%;
    justify-content: center;
    position: relative;
    margin-bottom: -80px;
}
.footer {
    margin-top: 120px;
}
.headerFirstPage p {
    font-size: 15px;
    font-weight: bold;
}

.logo p {
    font-weight: bold;
    color: red;
    font-size: 80px;
    margin-top: 35px;
    font-family: 'Jost',
    sans-serif;
}
.logo sup {
    font-size: 35px;
}
.logo img {
    width: 310px;
}
.cardBlock p {
    margin-top: 0px;
    margin-bottom: 0px;
}
.cardBlock img {
    width: 200px;
}
.footer p {
    font-size: 10px;
}
.cardBlock p {
    text-align: center;
}
.cardBlock p span:first-child {
    font-size: 35px;
}
.cardBlock p span:nth-child(2) {
    font-size: 23px;
    position: relative;
    top: -13px;
}
.footer p {
    margin: 0px;
}