.secondPage {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.header {
    height: 80px;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    background-color: white;
    align-items: end;
}   
.left, .right {
    width: 25%;
    background-color: #4ac7e3
;
}
.middleTop {
    background-color: #4ac7e3;
    text-align: center;
    padding-top: 20px;
    font-size: 14px;
;
}
.midlleBottom {
    background-color: white;
    width: 207px;
    height: 6%;
    border-radius: 7px 7px 0px 0px;
    margin-top: 47px;
    text-align: center;
    font-weight: bold;
    color: #4ac7e3;
    font-size: 27px;
    padding-top: 5px;
    position: absolute;
}
.middle {
    width: 50%;
    height: 75%;
    display: flex;
    flex-direction: column;
    background-color: #4ac7e3;
    margin-left: -1px;
    margin-right: -1px;
    color: #425262
;
}
.left, .right {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.left {
    border-radius: 0px 0px 7px 0px;
}
.right {
    border-radius: 0px 0px 0px 7px;
}
.right img, .left img {
    width: 50px;
}
.main {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #425262;
    margin-top: 19px;
    height: 450px;
}
.info {
    display: flex;
    flex-direction: row;
    width: 100%;
}

.leftSide {
    width: 51%;
    font-size: 20px;
    padding-right: 5px;
}
.rightSide {
    width: 49%;
    font-size: 25px;
    padding-left: 5px;
    
}
.leftSide div,
.rightSide div {
    height: 30%;
    font-weight: bold;
    display: flex;
    align-items: center;
}
.leftSide div p,
.rightSide div p {
    margin:0px;
}
.leftSide div {
    justify-content: flex-end;
    text-align: end;
}
.rightSide div {
    justify-content: flex-start;
}
.pageTitle {
    font-weight: 500;
    font-size: 20px;
    margin:0px;
}
.likeButton {
    background-color: #ff5256;
    width: 80%;
    height: 40px;
    border-radius: 10px;
    color: white;
    text-align: center;
    font-weight: bold;
    font-size: 23px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.thisPageFooter {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #425262;
    font-size: 10px;
    margin-top: 100px;
}
.thisPageFooter p {
    margin: 0px;
}
.connected {
    color: green;
}