@import url(https://fonts.googleapis.com/css2?family=Jost:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; 
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
html {
  height: 100%;
}

#root {
  height: 100%;
}
.App {
  display: flex;
  flex-direction: column;
  /* justify-content: center; */
  height: 100%;
}
.firstPage, 
.headerFirstPage,
.logo,
.cardBlock,
.footer {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #425262;
}
.headerFirstPage {
    height: 20%;
    justify-content: center;
    position: relative;
    margin-bottom: -80px;
}
.footer {
    margin-top: 120px;
}
.headerFirstPage p {
    font-size: 15px;
    font-weight: bold;
}

.logo p {
    font-weight: bold;
    color: red;
    font-size: 80px;
    margin-top: 35px;
    font-family: 'Jost',
    sans-serif;
}
.logo sup {
    font-size: 35px;
}
.logo img {
    width: 310px;
}
.cardBlock p {
    margin-top: 0px;
    margin-bottom: 0px;
}
.cardBlock img {
    width: 200px;
}
.footer p {
    font-size: 10px;
}
.cardBlock p {
    text-align: center;
}
.cardBlock p span:first-child {
    font-size: 35px;
}
.cardBlock p span:nth-child(2) {
    font-size: 23px;
    position: relative;
    top: -13px;
}
.footer p {
    margin: 0px;
}
.secondPage {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.header {
    height: 80px;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    background-color: white;
    align-items: end;
}   
.left, .right {
    width: 25%;
    background-color: #4ac7e3
;
}
.middleTop {
    background-color: #4ac7e3;
    text-align: center;
    padding-top: 20px;
    font-size: 14px;
;
}
.midlleBottom {
    background-color: white;
    width: 207px;
    height: 6%;
    border-radius: 7px 7px 0px 0px;
    margin-top: 47px;
    text-align: center;
    font-weight: bold;
    color: #4ac7e3;
    font-size: 27px;
    padding-top: 5px;
    position: absolute;
}
.middle {
    width: 50%;
    height: 75%;
    display: flex;
    flex-direction: column;
    background-color: #4ac7e3;
    margin-left: -1px;
    margin-right: -1px;
    color: #425262
;
}
.left, .right {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.left {
    border-radius: 0px 0px 7px 0px;
}
.right {
    border-radius: 0px 0px 0px 7px;
}
.right img, .left img {
    width: 50px;
}
.main {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #425262;
    margin-top: 19px;
    height: 450px;
}
.info {
    display: flex;
    flex-direction: row;
    width: 100%;
}

.leftSide {
    width: 51%;
    font-size: 20px;
    padding-right: 5px;
}
.rightSide {
    width: 49%;
    font-size: 25px;
    padding-left: 5px;
    
}
.leftSide div,
.rightSide div {
    height: 30%;
    font-weight: bold;
    display: flex;
    align-items: center;
}
.leftSide div p,
.rightSide div p {
    margin:0px;
}
.leftSide div {
    justify-content: flex-end;
    text-align: end;
}
.rightSide div {
    justify-content: flex-start;
}
.pageTitle {
    font-weight: 500;
    font-size: 20px;
    margin:0px;
}
.likeButton {
    background-color: #ff5256;
    width: 80%;
    height: 40px;
    border-radius: 10px;
    color: white;
    text-align: center;
    font-weight: bold;
    font-size: 23px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.thisPageFooter {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #425262;
    font-size: 10px;
    margin-top: 100px;
}
.thisPageFooter p {
    margin: 0px;
}
.connected {
    color: green;
}
